import React, { Component } from 'react'
import { NavBar, Icon, Tabs, Carousel, Flex } from 'antd-mobile';


export default class Home extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{ backgroundColor: '#3fcccb', color: '#fff' }}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />
                    ]}
                >住吧家居</NavBar>
                <Carousel
                    autoplay={true}
                    infinite
                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    afterChange={index => console.log('slide to', index)}
                    >
                    {[1,2].map(val => (
                        <a
                        key={val}
                        href="http://www.alipay.com"
                        style={{ display: 'inline-block', width: '100%', height: 200 }}
                        >
                        <img src={`./image/a${val}.png`} style={{ width: '100%', verticalAlign: 'top' }} />
                        </a>
                    ))}
                </Carousel>
                <div style={{height:'140px',backgroundColor:'#eeeeee'}}>
                    {[1,2,3].map(pic=>(
                        <img key={pic} src={`./image/s${pic}.png`} style={{ float:'left',margin:'10px 0px 10px 10px',width:'30%'}} />
                    ))}
                </div>
                <div style={{height:'60px'}}>
                    <img src={`./image/s7.png`} style={{ float:'left',marginTop:'10px'}} />
                    <h2 style={{marginLeft:'20px',fontSize:'16px',paddingTop:'15px'}}>热门推荐</h2>
                </div>
                <div style={{height:'200px',position:'relative'}}>
                    <img src={`./image/s6.png`} style={{ width:'100%'}} />
                    <p style={{position:'absolute',top:'130px',color:'#fff',marginLeft:'5%'}}>什么是英伦装修风格 英伦风家装 英伦风格装修效果图</p>
                </div>
            </div>
        )
    }
}
