import React, { Component } from 'react'
import { NavBar, Carousel, SearchBar } from 'antd-mobile';


export default class Shang extends Component {
    state = {
        value: '',
    };
    onChange = (value) => {
        this.setState({ value });
    };
    clear = () => {
        this.setState({ value: '' });
    };
    handleClick = () => {
        this.manualFocusInst.focus();
    }
    render() {
        return (
            <div>
                <NavBar
                    style={{ backgroundColor: '#3fcccb', color: '#fff' }}
                    rightContent={[
                        <div key="2" className='iconfont icon-gouwuche' style={{ marginRight: '16px', color: 'white' }}></div>
                    ]}
                >商城</NavBar>
                <SearchBar placeholder="插入关键字搜索" maxLength={8} />
                <Carousel
                    autoplay={true}
                    infinite
                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    afterChange={index => console.log('slide to', index)}
                >
                    {[1, 2, 3, 4].map(val => (
                        <a
                            key={val}
                            href="http://www.alipay.com"
                            style={{ display: 'inline-block', width: '100%', height: 180 }}
                        >
                            <img src={`./image/c${val}.png`} style={{ width: '100%', verticalAlign: 'top' }} />
                        </a>
                    ))}
                </Carousel>
                <div style={{ height: '170px' }}>
                    {['桌', '床', '椅', '几', '柜', '书架', '沙发', '家居饰品', '户外家具', '全部分类'].map(name => (
                        <div key={name} style={{ width: '50px', height: '65px', float: 'left', marginLeft: '20px', marginTop: '15px' }}>
                            <img src={`./image/${name}.png`} style={{ width: '50px', height: '50px' }} />
                            <p style={{ textAlign: 'center', fontSize: '12px' }}>{name}</p>
                        </div>
                    ))}
                </div>
                <div style={{ backgroundColor: '#eeeeee', height: '200px' }}>
                    <div style={{ float: 'left', width: '175px', height: '180px', fontSize: '12px', marginTop: '10px', marginLeft: "8px", textAlign: "center" }}>
                        <img src={`./image/c3.png`} style={{ width: '175px', height: '120px' }} />
                        <p>Too Art Studio 欧式风格精细...</p>
                        <p>¥39.95</p>
                    </div>
                    <div style={{ float: 'left', width: '175px', height: '180px', fontSize: '12px', marginTop: '10px', marginLeft: "8px", textAlign: 'center' }}>
                        <img src={`./image/c4.png`} style={{ width: '175px', height: '120px' }} />
                        <p>顺顺工艺欧式风格塑料外框黑...</p>
                        <p>¥83.99</p>
                    </div>
                </div>
                <div>
                    <img src={`./image/c5.png`} style={{ width: '100%' }} />
                </div>
            </div>
        )
    }
}
