import React, { Component } from 'react'
import { NavBar, Icon, Tabs, Carousel, Flex } from 'antd-mobile';

const tabs = [
    { title: '推荐' },
    { title: '冬季' },
    { title: '宜家' },
    { title: '小清新' },
    { title: '小户型' },
    { title: '个性色彩' },
    { title: '彪' }
];

export default class Ling extends Component {
    render() {
        return (
            <div style={{backgroundColor:'#eeeeee'}}>
                <NavBar
                    style={{ backgroundColor: '#3fcccb', color: '#fff' }}
                    rightContent={[
                        <Icon key="1" type="search" style={{ marginRight: '16px',color:'white'  }} />
                    ]}
                >灵感</NavBar>
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                >
                    <div>
                        {['橙色律动','儿童房','翻滚吧，地毯君','角落里的遐想','aaa','bbb'].map(name=>(
                            <div key={name} style={{width:'170px',height:'200px',float:'left',marginTop:'10px',marginLeft:'10px',position:'relative'}}>
                                <img src={`./image/橙色律动.png`} style={{width:'170px',height:'200px'}}/>
                                <p style={{position:'absolute',bottom:'-5px',left:'40px'}}>{name}</p>
                                <img src={`./image/l2.png`} style={{width:'15px',position:'absolute',right:'5px',bottom:'10px'}}/>
                            </div>
                        ))}
                    </div>

                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of second tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of third tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of forth tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of fivth tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of sixth tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of seventh tab
                    </div>
                </Tabs>
            </div>
        )
    }
}
